@font-face {
    font-family: 'HakgyoansimWoojuR';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-2@1.0/HakgyoansimWoojuR.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'YeongdeokSea';
    src: url('./fonts/Yeongdeok-Sea.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

/* reset */

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
    box-sizing: border-box;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

a{
    display: block;
    text-decoration: none;
    color: inherit;
}

button{
    background: none;
    border: none;
    cursor: pointer;
    color: inherit;
}

img{
    width: 100%;
}

/* content 스타일 시작 */
::selection{
    color: #fff;
    background-color: #da5d33;
}
input{
    background: none;
    border: none;
    text-align: center;
    font-family: 'YeongdeokSea','Arial','sans-serif';
}
input:focus{
    outline: none;
}
textarea{
    background: #fbe5da;
    padding: 20px;
    border-radius: 20px;
    border: none;
    font-family: 'YeongdeokSea','Arial','sans-serif';
}

html, body {
    margin: 0;
    padding: 0;
    font-family: 'YeongdeokSea','Arial','sans-serif';
    line-height: 1.2;
    min-height: 100vh;
}

body{
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(180deg, rgba(255,128,0,0.033), 3em, rgba(255,216,0,0) 33em), linear-gradient( 90deg, hsla(34deg,0%,96%,0.98) 5%, 10%, hsla(34deg,38%,99%,0.95) 25%, 33%, hsla(34deg,38%,99%,1) 50%, 67%, hsla(34deg,38%,99%,0.975) 75%, 90%, hsla(34deg,0%,96%,0.98) 95% ) 0 0 / 100% 100% no-repeat, linear-gradient(179deg, hsla(34deg,38%,99%,1) 11em, hsla(34deg,38%,99%,0) 13em), linear-gradient(0deg, hsla(34deg,38%,99%,1) 23em, hsla(34deg,38%,99%,0) 42em), url(./img/bg-waves.png) 0 0, url(./img/bg-waves.png) 250px -50px, hsla(34deg,38%,99%,1);
}

body #root{
    position: relative;
    width: 100%;
    max-width: 900px;
    padding:4em 2em;
}

body .App{
    position: relative;
    overflow: hidden;
}

header{
    position: relative;
    font-family: 'HakgyoansimWoojuR';
    font-weight: bold;
    padding-top: 20px;
}

header > h1 a,
.title_edit{
    font-size: 2em;
    color: #1f1f1f;
    font-family: 'HakgyoansimWoojuR';
    font-weight: bold;
}

header > strong,
.sub_edit,.modify_btn{
    color: #da5d33;
    font-weight: bold;
}
.modify_btn{
    font-family: 'HakgyoansimWoojuR';
}
.modify_btn:nth-of-type(2){
    top: 40px
}
.sub_edit{
    width: 100%;
    font-size: 20px;
    font-weight: bold;
    font-family: 'HakgyoansimWoojuR';
    background: #fbe5da;
    padding: 10px 0;
    border-radius: 20px;
}
.title_edit{
    max-width: 100%;
    margin: 10px 0 20px;
    background: #fbe5da;
    padding: 10px 0;
    border-radius: 20px;
}

article{
    margin-top: 50px;
    text-align: center;
    color: #1f1f1f;
    /* background: #fbe5da; */
}

article h2{
    display: inline-block;
    max-width: 100%;
    font-size: 2.5em;
    font-weight: bold;
    color: #da5d33;
    padding: 10px 0;
    overflow: hidden;
    word-break: keep-all;
}
article h2::after{
    content: "";
    position: relative;
    display: block;
    margin-top: -10px;
    width: 100%;
    height: 5px;
    border-radius: 10px;
    background: #fbe5da;
    z-index: -1;
}

article h2 input{
    display: block;
    width: 100%;
    font-size: 0.7em;
    font-weight: bold;
    color: #da5d33;
}

article > p,
article >  form{
    font-size: 24px;
    line-height: 40px;
    text-align: left;
    /* background: #fbe5da; */
    margin: 20px 0 0;
    border-radius: 20px;
    word-break: keep-all;
}
article > p{
    background: url(./img/b-simple-L2.png);
    background-size: 100% 40px;
    background-repeat: repeat-y;
    word-break: keep-all;
    margin-bottom: 80px;
}
article >  form{
    text-align: center;
}
article >  form textarea{
    width: 100%;
    height: 50vh;
    font-size: 24px;
    line-height: 40px;
    resize: none;
    box-sizing: border-box;
}

.modify_btn{
    position: absolute;
    top:20px; right:0;
}

ul.post_edit{
    position: absolute;
    bottom:0px; left:0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0 2em;
}
ul.post_edit > div{
    display: flex;
    gap: 20px;
}

button.submit,
ul.post_edit a,
ul.post_edit button{
    position: relative;
    font-family: 'HakgyoansimWoojuR';
    font-size: 1.2em;
    font-weight: bold;
    color: #da5d33;
}
button.submit::before,
ul.post_edit a::before,
ul.post_edit button::before,
ul.post_edit a::after,
ul.post_edit button::after{
    content: "";
    width: 100%; height: 5px;
    display: block;
    position: absolute;
    bottom:3px; left: 0;
    background: #fbe5da;
    z-index: -1;
    transition: all ease 0.2s;
}
button.submit::after,
ul.post_edit a::after,
ul.post_edit button::after{
    transform: scaleX(0);
    transform-origin: top left;
    background: #da5d33;
    opacity: 0.7;
}
button.submit:hover::after,
ul.post_edit a:hover::after,
ul.post_edit button:hover::after{
    transform: scaleX(100%);
}

.post_date{
    display: block;
}

nav{
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 99;
}


nav > input[type="checkbox"]{
    position: absolute;
    bottom: 6vh;
    right: 6vw;
    z-index: 90;
}
nav > input[type="checkbox"]::after{
    content: "+";
    display: block;
    background: #da5d33;
    text-align: center;
    font-family: 'HakgyoansimWoojuR';
    color: #fff;
    font-size: 25px;
    line-height: 40px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    position: absolute;
    top: -1vw;
    left: -2vh;
    transition: all ease 0.2s;
    cursor: pointer;
}
nav > input[type="checkbox"]:checked::after{
    transform: scale(1.1) rotate(135deg);
}

nav > div{
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%; max-height: 100%;
    overflow-y: scroll;
    padding:4vh 4vw 100px;
    box-sizing: border-box;
    background: rgba(247, 246, 245,0.9);
    z-index: 80;
}

body::-webkit-scrollbar,
nav > div::-webkit-scrollbar,
article > form textarea::-webkit-scrollbar{
    width: 5px;
}
nav > div::-webkit-scrollbar-thumb,
article > form textarea::-webkit-scrollbar-thumb{
    background-color: #fbe5da;
    border-radius: 20px;
}

body::-webkit-scrollbar-thumb{
    background-color: #d1c0b7;
    border-radius: 20px;
}
body::-webkit-scrollbar-track,
nav > div::-webkit-scrollbar-track{
    background-color:rgb(247, 246, 245);
}
article > form textarea::-webkit-scrollbar-thumb{
    background-color: #da5d33;
}

article > form textarea::-webkit-scrollbar-button:vertical:start:decrement{
    display: block;
    height: 20px;
}

article > form textarea::-webkit-scrollbar-button:vertical:end:decrement {
    display: block;
    width: 20px;
}

nav > input[type="checkbox"]:checked + div{
    display: block;
}

nav strong{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'HakgyoansimWoojuR';
    font-size:30px;
    font-weight: bold;
    padding-bottom: 10px;
}

nav ul{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}
nav ul li{
    background:#fbe5da;
    padding:10px 20px;
    border-radius: 20px;
    color: #1f1f1f;
    transition: all ease 0.2s;
}
nav ul li:last-of-type{
    color: #fff;
    background: #da5d33;
}
nav ul li:hover{
    transform: translateY(5px);
}